const initialState = {
    check2: false
  }
 export const check = (state = initialState, action) => {
      switch (action.type){
          case 'CHECK_BOX':
          console.warn("action done")
              return {
                  ...state,
                  check2: !state.check2,
              }        
          default:
              return state
      }
  }

  export default check